/*
*   Background
*/
.background {
  background-color: pink;
  height: 100%;
  margin-top: 0;
  width: 100%;
  position: fixed;
}

/*
*   Center Text
*/
h1 {
  left: 0;
  line-height: 300px;
  margin-top: -100px;
  position: absolute;
  text-align: center;
  top: 48%;
  width: 100%;
  animation: fade 4s ease-in;
  color: #fa1b87;
  font-size: 100px;
  font-family: 'Monaco';
}

h2 {
  left: 0;
  line-height: 200px;
  margin-top: -100px;
  position: absolute;
  text-align: center;
  top: 42%;
  width: 100%;
  animation: fade 4s ease-in;
  color: #fa1b87;
  font-size: 30px;
  font-family: 'Monaco';
}

@keyframes fade {
  0% {
    color: white;
  }
  100% {
    color: #fa1b87;
  }
}
